:root {
  --color-dark: #1B1C21;
  --color-dark-alt: #292A32;
  --color-light: #FFFFFF;
  --color-light-green: #41F6D7;
  --color-gray-dashes: #EAEAEA;
  --color-gray: #F8F8F8;
  --color-gray-alt: #7E7E82;
  --color-purple: #FF3E79;
  --color-light-green-alt: #B8FFEC;
  --font-family-base: 'TT Lakes';
  --container-width: 1140px;
  --container-padding-x: 20px;
  --transition-duration: 0.2s; }

body {
  font-size: clamp(16px, 1.25vw, 18px);
  font-family: var(--font-family-base);
  font-weight: 400;
  line-height: 1.28;
  color: var(--color-dark);
  background-color: var(--color-gray);
  letter-spacing: -0.2px;
  font-size: 16px; }

a,
button,
input,
textarea,
svg * {
  transition-duration: var(--transition-duration); }

a {
  text-decoration: none;
  color: inherit; }

h1, h2, h3, h4 {
  font-weight: 500; }

h1, .h1 {
  font-size: clamp(43px, 4.16667vw, 60px); }

h2, .h2 {
  font-size: clamp(36px, 2.77778vw, 40px); }

h3, .h3 {
  font-size: clamp(26px, 2.08333vw, 30px); }

h4, .h4 {
  font-size: clamp(18px, 1.38889vw, 20px); }

button {
  border: none;
  background-color: inherit; }
  @media (hover: hover) {
    button:hover {
      opacity: 0.8; } }
  @media (hover: none) {
    button:active {
      opacity: 0.8; } }

@media (hover: hover) {
  a:hover {
    color: var(--color-purple); } }

@media (hover: none) {
  a:active {
    color: var(--color-purple); } }
