.prices {
  padding: 20px; }
  .prices__cards-mobile {
    margin-bottom: 8px; }
    @media (min-width: 768px) {
      .prices__cards-mobile {
        display: none !important; } }
  .prices__cards-desktop {
    display: none; }
    @media (min-width: 768px) {
      .prices__cards-desktop {
        display: flex;
        flex-direction: column;
        margin-bottom: 28px; } }
    .prices__cards-desktop--title {
      display: grid;
      grid-template-columns: minmax(395px, 1fr) minmax(90px, 1fr) minmax(81px, 1fr) minmax(119px, 1fr);
      align-items: center;
      justify-content: center;
      margin-bottom: 16px; }
    .prices__cards-desktop--values {
      display: grid;
      min-width: 720px;
      min-height: 324px;
      justify-content: center;
      align-items: center;
      grid-template: repeat(5, minmax(64px, 1fr))/minmax(395px, 1fr) minmax(80px, 1fr) minmax(81px, 1fr) minmax(119px, 1fr);
      border-radius: 6px;
      background-color: var(--color-dashes);
      white-space: nowrap;
      row-gap: 1px; }
      .prices__cards-desktop--values > div {
        background-color: var(--color-light);
        height: 100%;
        display: flex;
        align-items: center;
        padding-inline: 16px;
        border-radius: 6px; }
  .prices__list {
    -moz-column-gap: 16px;
         column-gap: 16px; }
  .prices__item {
    min-width: 260px;
    min-height: 200px;
    padding: 16px;
    border-radius: 6px;
    background: var(--color-light);
    position: relative;
    display: flex !important;
    flex-direction: column;
    gap: 16px; }
  .prices__text--top {
    display: flex;
    flex-direction: column; }
  .prices__text--center {
    display: flex;
    flex-direction: column; }
  .prices__text--bottom {
    display: flex;
    flex-direction: column; }
  .prices__subscription {
    margin-bottom: 16px; }
  .prices__button-offer {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    white-space: nowrap;
    padding: 0;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: -0.2px; }
    @media (hover: hover) {
      .prices__button-offer:hover {
        color: var(--color-purple); } }
    @media (hover: none) {
      .prices__button-offer:active {
        color: var(--color-purple); } }
  .prices__subscription {
    color: #1b1c21;
    font-family: TT Lakes;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.2px;
    text-align: left;
    max-width: 421px; }
    @media (min-width: 768px) {
      .prices__subscription {
        max-height: 60px; } }

.buy-button {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-width: 119px;
  height: 32px;
  border-radius: 16px;
  background: var(--color-purple);
  color: var(--color-light);
  font-size: 12px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.15px;
  text-transform: uppercase; }
  @media (max-width: 767px) {
    .buy-button {
      position: absolute;
      bottom: 16px;
      right: 16px; } }

.swiper-pagination {
  position: relative !important; }
