.header {
  position: fixed;
  width: 100%;
  justify-content: space-around;
  padding: 24px;
  background-color: var(--color-light);
  max-height: 88px;
  min-width: 320px;
  z-index: 100; }
  @media (min-width: 1440px) {
    .header {
      display: none; } }
  .header__burger {
    margin-right: 20px; }
    @media (max-width: 767px) {
      .header__burger {
        margin-right: 0px; } }
  .header__center {
    position: relative;
    flex: 1;
    justify-content: space-between;
    padding-inline: 20px; }
    @media (max-width: 767px) {
      .header__center {
        padding-inline: 16px; } }
    @media (min-width: 768px) {
      .header__center::before {
        content: '';
        position: absolute;
        width: 2px;
        height: 32px;
        border-radius: 1px;
        background: var(--color-gray-dashes);
        left: 0;
        top: 4px; } }
    .header__center::after {
      content: '';
      position: absolute;
      width: 2px;
      height: 32px;
      border-radius: 1px;
      background: var(--color-gray-dashes);
      right: 0;
      top: 4px; }
    .header__center-feedback {
      display: flex;
      -moz-column-gap: 16px;
           column-gap: 16px; }
      @media (max-width: 767px) {
        .header__center-feedback {
          display: none; } }
  .header__callback {
    display: flex;
    margin-left: 20px;
    justify-content: space-around;
    -moz-column-gap: 16px;
         column-gap: 16px; }
