.footer {
  display: flex;
  flex-direction: column;
  row-gap: 16px;
  opacity: 0.72;
  padding-top: 0;
  margin-top: 42px;
  align-items: center;
  justify-content: center;
  color: var(--color-gray-alt); }
  @media (max-width: 767px) {
    .footer {
      padding-inline: 4px; } }
  @media (min-width: 768px) {
