.callback-modal {
  position: fixed;
  max-width: 0;
  top: 0;
  background-color: rgba(255, 255, 255, 0.9);
  transition: 0.5s ease-in-out;
  width: 100%;
  height: 100%;
  z-index: 10000;
  display: flex;
  justify-content: flex-end; }
  .callback-modal__close {
    position: absolute;
    left: -65px;
    top: 35px; }
    @media (max-width: 767px) {
      .callback-modal__close {
        top: 24px;
        left: 16px; } }
    .callback-modal__close--button {
      position: relative; }
      .callback-modal__close--button span {
        position: absolute;
        width: 21px;
        height: 2px;
        background-color: var(--color-light);
        position: absolute;
        top: 50%;
        left: 50%;
        translate: -50% -50%; }
        .callback-modal__close--button span:nth-child(1) {
          transform: rotate(45deg); }
        .callback-modal__close--button span:nth-child(2) {
          transform: rotate(-45deg); }

.callback-modal.active-modal {
  right: 0;
  max-width: 100%;
  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2), -2px 0px 4px 0px rgba(69, 79, 126, 0.02); }

body.active-modal {
  overflow: hidden; }

.callback-menu {
  transition: 0.5s ease-in-out;
  position: relative;
  top: 0;
  left: 0;
  max-width: 440px;
  display: flex;
  flex-direction: column;
  padding: 40px 32px;
  row-gap: 16px;
  height: 100%;
  background-color: var(--color-light);
  background: white;
  overflow-y: scroll;
  scrollbar-width: none;
  overflow: visible; }
  .callback-menu::-webkit-scrollbar {
    width: 0;
    background: transparent; }
  .callback-menu__title {
    position: relative;
    margin-bottom: 8px;
    color: #1b1c21;
    font-family: TT Lakes;
    font-size: 24px;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: -0.6px; }
    @media (max-width: 767px) {
      .callback-menu__title {
        margin-top: 80px; } }
    .callback-menu__title::before {
      content: '';
      position: absolute;
      top: -11px;
      left: -32px;
      border-radius: 2px;
      background: var(--color-light-green);
      width: 4px;
      height: 40px; }
  .callback-menu__input {
    border: 1px solid var(--color-gray-dashes);
    border-radius: 8px;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: -0.2px;
    padding: 12px 16px;
    min-width: 100%; }
  .callback-menu__agreement a {
    color: var(--color-purple); }
  .callback-menu__button-block {
    display: flex;
    justify-content: flex-end;
    align-items: center; }
  .callback-menu__button {
    position: unset; }
