.menu {
  padding: 30px 30px 20px 23px;
  height: 100%;
  top: 0;
  z-index: 10000;
  min-width: 305px;
  max-width: 100%;
  background-color: var(--color-light);
  position: fixed; }
  .menu-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow-y: scroll;
    scrollbar-width: none;
    max-height: 95vh;
    width: 100%;
    overflow: visible; }
    .menu-container::-webkit-scrollbar {
      width: 0;
      background: transparent; }
  .menu-wrapper {
    min-height: 100%;
    position: relative;
    background-color: var(--color-light); }
    @media (max-width: 1439px) {
      .menu-wrapper {
        top: 0;
        position: fixed;
        left: -1200px;
        overflow: hidden;
        transition: 0.5s; } }
  .menu__search {
    justify-content: space-between;
    align-items: center;
    margin-bottom: 37px;
    gap: 16px; }
    .menu__search-logo {
      flex: 1; }
  .menu__nav {
    flex: 1;
    margin-bottom: 32px;
    flex: 1;
    overflow: visible; }
  .menu__list {
    display: flex;
    flex-direction: column;
    row-gap: 28px;
    overflow: visible; }
  .menu .item-active {
    position: relative;
    color: var(--color-gray-alt);
    overflow: visible; }
    .menu .item-active::before {
      content: '';
      position: absolute;
      top: -3px;
      left: -23px;
      border-radius: 2px;
      background: var(--color-light-green);
      width: 4px;
      height: 28px; }
  .menu__link {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px; }

.feedback__buttons {
  -moz-column-gap: 16px;
       column-gap: 16px;
  margin-bottom: 16px; }

.feedback__email--link {
  font-weight: 500;
  line-height: 24px;
  margin-bottom: 12px; }

.feedback__number--link {
  font-size: 24px;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: -0.6px;
  margin-bottom: 40px; }

.lang {
  display: flex;
  -moz-column-gap: 7px;
       column-gap: 7px; }
  @media (hover: hover) {
    .lang__ru:hover {
      color: var(--color-purple);
      cursor: pointer; } }
  @media (hover: none) {
    .lang__ru:active {
      color: var(--color-purple);
      cursor: pointer; } }
  @media (hover: hover) {
    .lang__en:hover {
      color: var(--color-purple);
      cursor: pointer; } }
  @media (hover: none) {
    .lang__en:active {
      color: var(--color-purple);
      cursor: pointer; } }
  @media (hover: hover) {
    .lang__cn:hover {
      color: var(--color-purple);
      cursor: pointer; } }
  @media (hover: none) {
    .lang__cn:active {
      color: var(--color-purple);
      cursor: pointer; } }

@media (min-width: 1440px) {
  .main-container {
    max-width: 1440px;
    display: grid;
    grid-template-columns: 305px 1fr; } }

.menu-wrapper.menu-active {
  top: 0;
  left: 0;
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  transition: 0.5s; }
  .menu-wrapper.menu-active > .menu {
    box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2);
    position: absolute;
    max-width: 320px;
    background-color: var(--color-light); }
    @media (max-width: 360px) {
      .menu-wrapper.menu-active > .menu {
        max-width: 360px;
        width: 100%; } }

.close-button {
  position: relative;
  display: none; }
  .close-button.active-button {
    display: block; }
  .close-button span {
    position: absolute;
    width: 21px;
    height: 2px;
    background-color: var(--color-light);
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%; }
    .close-button span:nth-child(1) {
      transform: rotate(45deg); }
    .close-button span:nth-child(2) {
      transform: rotate(-45deg); }

.white-space {
  width: 100%; }
