:root {
  --color-dark: #1B1C21;
  --color-dark-alt: #292A32;
  --color-light: #FFFFFF;
  --color-light-green: #41F6D7;
  --color-gray-dashes: #EAEAEA;
  --color-gray: #F8F8F8;
  --color-gray-alt: #7E7E82;
  --color-purple: #FF3E79;
  --color-light-green-alt: #B8FFEC;
  --font-family-base: 'TT Lakes';
  --container-width: 1140px;
  --container-padding-x: 20px;
  --transition-duration: 0.2s; }

.wrapper {
  max-width: 1440px;
  margin: 0 auto; }

.container {
  max-width: 1440px; }
  @media (min-width: 1440px) {
    .container {
      max-width: 1120px;
      margin-inline: auto; } }

.main-container {
  max-width: 1440px; }

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  white-space: nowrap !important;
  -webkit-clip-path: inset(100%) !important;
          clip-path: inset(100%) !important;
  clip: rect(0 0 0 0) !important;
  overflow: hidden !important; }

@media (max-width: 767px) {
  .hidden-mobile {
    display: none !important; } }

@media (min-width: 768px) {
  .visible-mobile {
    display: none !important; } }

.flex {
  display: flex; }
  .flex-center {
    display: flex;
    justify-content: center;
    align-items: center; }

@media (max-width: 1439px) {
  .main {
    max-width: 1439px;
    width: 100%; } }

.padding-container {
  padding: 42px 30px 30px 30px; }
  @media (max-width: 1439px) {
    .padding-container {
      padding-inline: 24px; } }
  @media (max-width: 767px) {
    .padding-container {
      padding-inline: 16px; } }

.section-button {
  position: relative;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
  padding-left: 30px;
  margin-bottom: 16px; }
  @media (hover: hover) {
    .section-button:hover {
      color: var(--color-purple); } }
  @media (hover: none) {
    .section-button:active {
      color: var(--color-purple); } }
  .section-button::before {
    content: url(./static/two-arrows-bottom.svg);
    position: absolute;
    left: 0; }

.open {
  max-height: 100% !important; }

.text-gray {
  opacity: 0.72; }
  @media (min-width: 768px) {
    .text-gray {
      padding-inline: 16px; } }
