.repair {
  padding-top: 0; }
  .repair__list.swiper-wrapper {
    -moz-column-gap: 16px;
         column-gap: 16px; }
    @media (min-width: 768px) {
      .repair__list.swiper-wrapper {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        row-gap: 16px;
        -moz-column-gap: 24px;
             column-gap: 24px;
        padding-bottom: 24px;
        overflow: hidden; } }
    @media (min-width: 1440px) {
      .repair__list.swiper-wrapper {
        grid-template-columns: repeat(4, 1fr); } }
  .repair__item {
    box-sizing: border-box;
    border: 1px solid #eaeaea;
    border-radius: 6px;
    background: var(--color-light); }
    @media (hover: hover) {
      .repair__item:hover {
        transform: translateY(2px);
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); } }
    @media (hover: none) {
      .repair__item:active {
        transform: translateY(2px);
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); } }
    @media (min-width: 768px) {
      .repair__item {
        max-width: 224px; } }
    @media (min-width: 1440px) {
      .repair__item {
        max-width: 240px; } }
  .repair__link {
    min-width: 240px;
    min-height: 160px;
    padding: 16px;
    position: relative; }
    @media (min-width: 768px) {
      .repair__link {
        min-width: 224px; } }
    @media (min-width: 1440px) {
      .repair__link {
        min-width: 240px; } }
  @media (max-width: 767px) {
    .repair__button {
      display: none; } }

.repair__hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  white-space: nowrap !important;
  -webkit-clip-path: inset(100%) !important;
          clip-path: inset(100%) !important;
  clip: rect(0 0 0 0) !important;
  overflow: hidden !important; }

.link__arrow {
  position: absolute;
  bottom: 16px;
  right: 16px; }
