.brands {
  margin-top: 20px;
  padding-bottom: 16px; }
  @media (min-width: 1440px) {
    .brands {
      margin-top: 0; } }
  .brands__list.swiper-wrapper {
    -moz-column-gap: 16px;
         column-gap: 16px; }
    @media (min-width: 768px) {
      .brands__list.swiper-wrapper {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        row-gap: 16px;
        -moz-column-gap: 24px;
             column-gap: 24px;
        padding-bottom: 24px;
        overflow: hidden; } }
    @media (min-width: 1440px) {
      .brands__list.swiper-wrapper {
        grid-template-columns: repeat(4, 1fr); } }
  .brands__item {
    max-width: 240px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid var(--color-gray-dashes);
    border-radius: 6px;
    background: var(--color-light); }
    @media (hover: hover) {
      .brands__item:hover {
        transform: translateY(2px);
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); } }
    @media (hover: none) {
      .brands__item:active {
        transform: translateY(2px);
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); } }
    @media (min-width: 768px) {
      .brands__item.swiper-slide {
        max-width: 224px !important; } }
    @media (min-width: 1440px) {
      .brands__item.swiper-slide {
        max-width: 240px !important; } }
  .brands__link {
    min-width: 240px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 72px;
    padding-inline: 16px; }
    @media (min-width: 768px) {
      .brands__link {
        min-width: 194px; } }
  @media (max-width: 767px) {
    .brands__button {
      display: none; } }

.title {
  display: flex;
  margin-inline: -24px;
  padding-inline: 24px;
  margin-bottom: 24px;
  height: 56px;
  background: linear-gradient(90deg, #f8f8f8, white 98.614%); }
  @media (min-width: 1440px) {
    .title {
      margin-inline: -38px;
      padding-inline: 38px; } }
  @media (max-width: 767px) {
    .title {
      height: 80px; } }
  .title h2 {
    color: var(--color-gray-alt);
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: -0.2px;
    text-transform: uppercase; }

.swiper-pagination {
  position: relative;
  margin-top: 20px;
  text-align: center; }
  @media (min-width: 768px) {
    .swiper-pagination {
      display: none; } }

.swiper-slide {
  max-width: 240px; }
