@media (max-width: 1439px) {
  .services {
    position: relative;
    top: 70px;
    max-width: 1439px;
    width: 100%; } }

.services__title {
  justify-content: space-between;
  position: relative; }
  @media (max-width: 1439px) {
    .services__title {
      margin-bottom: 32px; } }
  @media (max-width: 767px) {
    .services__title {
      margin-bottom: 24px; } }
  .services__title::before {
    content: '';
    position: absolute;
    top: 0;
    left: -37.5px;
    border-radius: 2px;
    background: var(--color-light-green);
    width: 4px;
    height: 40px; }
    @media (max-width: 1439px) {
      .services__title::before {
        left: -24px; } }
    @media (max-width: 767px) {
      .services__title::before {
        left: -16px; } }

.services__list {
  -moz-column-gap: 5px;
       column-gap: 5px;
  flex-wrap: wrap;
  margin-bottom: 20px; }
  @media (max-width: 1439px) {
    .services__list {
      overflow-x: scroll;
      flex-wrap: nowrap; } }

.services__item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  box-sizing: border-box;
  border-radius: 6px;
  color: var(--color-gray-alt); }
  @media (hover: hover) {
    .services__item:hover {
      border: 2px solid var(--color-light-green-alt); }
      .services__item:hover .services__link {
        color: var(--color-dark); } }
  @media (hover: none) {
    .services__item:active {
      border: 2px solid var(--color-light-green-alt); }
      .services__item:active .services__link {
        color: var(--color-dark); } }

.services__link {
  color: var(--color-gray-alt);
  padding-inline: 20px;
  white-space: nowrap; }

.services__info {
  display: flex;
  justify-content: space-between; }
  @media (max-width: 767px) {
    .services__info {
      flex-direction: column; } }

.services__info--left {
  max-width: 490px;
  width: 100%; }
  @media (max-width: 1439px) {
    .services__info--left {
      max-width: 50%;
      margin-right: 48px; } }
  @media (max-width: 767px) {
    .services__info--left {
      max-width: 100%; } }

.services__info--text {
  max-height: 165px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  margin-bottom: 13px; }
  .services__info--text p {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal; }

@media (max-width: 767px) {
  .services__info--image {
    margin-inline: -16px; } }

.services__info--image img {
  object-fit: contain;
  border-radius: 5px;
  width: 100%; }
  @media (max-width: 767px) {
    .services__info--image img {
      border-radius: 0; } }

.title {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px; }
  .title__text {
    font-size: 28px;
    font-weight: 700;
    line-height: 40px;
    letter-spacing: -0.6px; }
  .title__feedback {
    display: flex;
    -moz-column-gap: 32px;
         column-gap: 32px; }
    @media (max-width: 1439px) {
      .title__feedback {
        display: none; } }
  .title__button {
    justify-content: space-between;
    align-items: center;
    -moz-column-gap: 8px;
         column-gap: 8px;
    transition: var(--transition-duration); }
    @media (hover: hover) {
      .title__button:hover {
        color: var(--color-purple);
        cursor: pointer; }
        .title__button:hover .purple-button {
          opacity: 0.8; } }
    @media (hover: none) {
      .title__button:active {
        color: var(--color-purple);
        cursor: pointer; }
        .title__button:active .purple-button {
          opacity: 0.8; } }
